import api from '@/api/axios';

const state = {
  loginInfo: null,
  isChecked: null,
};

const mutations = {
  setLoginInfo(state, loginInfo) {
    state.loginInfo = loginInfo;
  },
  clearLoginInfo(state) {
    state.loginInfo = null;
    state.isChecked = null; // isChecked 
  },
  setIsChecked(state, isChecked) {
    state.isChecked = isChecked;
  },
};

const actions = {
  async login({ commit }, loginInfo) {
    try {
      const response = await api.post('/user/login', loginInfo); // await     .
      const data = response.data; //      'data'  .
      // reponse.data.user{
      //  user_no: 
      //  user_email:  
      //  user_pw: 
      //  user_name: 
      //  user_profile:     
      //  user_year: 
      //  user_birth: 
      //  user_is_delete:    (0 or 1)
      //  user_is_manager:     (0 or 1)
      //  user_is_admin:     (0 or 1)
      //  dp_no:   
      //  level_no: (ex. L1)
      //  job_no: (ex. )
      //  position_no: (ex. )
      //  mile_no:    ,   (1~8)
      //}
      commit('setIsChecked', loginInfo.is_checked);
      if (response.status === 200) {
        const expirationTime = new Date().getTime() + 60 * 60 * 1000;
        const storageData = {
          loginInfo: data.user,
          isChecked: loginInfo.is_checked, // isChecked 
          expirationTime: expirationTime,
        };
        localStorage.setItem('loginInfo', JSON.stringify(storageData));
        commit('setLoginInfo', data.user);
        return data;
      } else {
        console.error('Login ', data.message);
        return null;
      }
    } catch (error) {
      console.error('error ', error);
      return null;
    }
  },
  logout({ commit }) {
    return new Promise((resolve) => {
      //    
      document.body.classList.add('fade-out');
      
      //     
      setTimeout(() => {
        localStorage.removeItem('loginInfo');
        commit('clearLoginInfo');
        
        //    
        document.body.classList.remove('fade-out');
        resolve();
      }, 300); //    
    });
  },
  checkLogin({ commit }) {
    const data = JSON.parse(localStorage.getItem('loginInfo'));
    if (data) {
      const currentTime = new Date().getTime();
      if (currentTime < data.expirationTime) {
        commit('setLoginInfo', data.loginInfo);
        commit('setIsChecked', data.isChecked); // isChecked 
      } else {
        localStorage.removeItem('loginInfo');
        commit('clearLoginInfo');
      }
    }
  },
  async sendEmail(context, inputInfo) { //       
    console.log('Input Info to send:', inputInfo);
    try {
      const response = await api.post('/user/pwreset', inputInfo);
      return response.data; //      
    } catch (error) {
      console.error('    ', error);
      return null;
    }
  },
};

const getters = {
  getLoginInfo: (state) => state.loginInfo,
  getIsChecked: (state) => state.isChecked,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
