// src/store/modules/user.js
import api from '@/api/axios';

const state = {
  //   
  arrayUsers: [],
  objectUser: null,
};

const mutations = {
  //setter  

  setArrayUsers(state, payload) {
    //
    //state : like this , payload :      <> )

    state.arrayUsers = payload;
    //this arrayUsers = (payload) 
  },
  setObjectUser(state, payload) {
    state.objectUser = payload;
  },
};

const actions = {
  async getUserById({ commit }, user_no) { //      
    try {
      const response = await api.get(`/user/${user_no}`);
      commit('setObjectUser', response.data);
    } catch (error) {
      console.error('Error get user data:', error);
    }
  },

  //  AllUserView -> this.$store.dispatch('user/getAllUser'); -> getAllUser
  async getAllUser({ commit }) {
    //      
    try {
      const response = await api.get('/user/allUser');
      //  user/allUser  ->    -> const response 

      commit('setArrayUsers', response.data);
      //  response.data  setArrayUsers   
      // commit :  () !
    } catch (error) {
      console.error('Error get All user data:', error);
    }
  },
};

const getters = {
  //  
  getArrayUsers: (state) => state.arrayUsers,
  getObjectUser: (state) => state.objectUser,
};

//index.js  
export default {
  namespaced: true, // true
  state,
  mutations,
  actions,
  getters,
};
