<template>
  <div id="app">
    <main v-if="!$route.meta.hideHeader">
      <HeaderAdmin
        v-if="
          isLoggedIn &&
          loginInfo.user_is_admin &&
          !loginInfo.user_is_manager &&
          isChecked
        "
        class="fixed-header"
      />
      <HeaderManager
        v-else-if="
          isLoggedIn &&
          !loginInfo.user_is_admin &&
          loginInfo.user_is_manager &&
          isChecked
        "
        class="fixed-header"
      />
      <HeaderUser v-else-if="isLoggedIn && !isChecked" class="fixed-header" />

      <router-view />

      <button @click.stop="openModal" class="chat-button">
        <img src="./assets/img/chat.gif" alt="Chat" class="chat-icon" />
      </button>
      <ChatModal v-if="isModalOpen" :isOpen="isModalOpen" @close="closeModal" />

      <button @click="scrollToTop" class="top-button">
        <i class="bi bi-chevron-up"></i>
      </button>
      <Footer :key="$route.fullPath" />
      <!-- TOP  -->
    </main>

    <router-view v-else />
    <!-- hideHeader true  main Header  router-view  -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import HeaderAdmin from './views/common/HeaderAdminView.vue';
import HeaderUser from './views/common/HeaderUserView.vue';
import HeaderManager from './views/common/HeaderManagerView.vue';
import Footer from './views/common/FooterView.vue';
import ChatModal from './ChatModal.vue';

export default {
  name: 'App',

  data() {
    return {
      isModalOpen: false,
    };
  },
  components: {
    HeaderAdmin,
    HeaderUser,
    HeaderManager,
    Footer,
    ChatModal,
  },
  computed: {
    ...mapGetters('login', ['getLoginInfo', 'getIsChecked']),
    loginInfo() {
      return this.getLoginInfo;
    },
    isChecked() {
      return this.getIsChecked;
    },
    isLoggedIn() {
      return !!this.loginInfo; // loginInfo null    .
    },
  },
  watch: {
    $route: {
      handler() {
        //        .
        console.log('Route changed');
        //      
        console.log('isLoggedIn:', this.isLoggedIn);
        console.log('loginInfo:', this.loginInfo);
        console.log('isChecked:', this.isChecked);
      },
      immediate: true, //      
    },
  },
  methods: {
    scrollToTop() {
      console.log('scrollToTop ');
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },

    openModal() {
      this.isModalOpen = true;
    },

    closeModal() {
      this.isModalOpen = false;
    },
  },
};
</script>

<style>
@import url('./assets/css/css.css');

/*   */
.chat-button {
  position: fixed;
  bottom: 90px;
  right: 20px;
  background-color: transparent;
  width: 50px; /*        */
  height: 50px;
  font-family: 'KB_C3', sans-serif;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9000;
}

.chat-icon {
  width: 50px; /*    */
  height: 50px;
  object-fit: cover; /*      */
}
</style>
